import { NgModule } from '@angular/core';
import { CommonModule } from '@angular/common';
import { FormsModule } from '@angular/forms';
import { Routes, RouterModule } from '@angular/router';

import { IonicModule } from '@ionic/angular';

import { GerinimigosPage } from './gerinimigos.page';
import { CriacoesModule } from '../components/criacoes/criacoes.module';
import { FichasModule } from '../components/ficha/fichas.module';
import { PaginacaoModule } from '../components/utils/paginacao/paginacao.module';

import { MessageService } from 'primeng/api';
import { ToastModule } from 'primeng/toast';
import { ImagemModule } from '../components/imagem/imagem.module';
import { UpdateNpcModule } from '../components/criacoes/update-npc/update-npc.module';

const routes: Routes = [
  {
    path: '',
    component: GerinimigosPage
  }
];

@NgModule({
  imports: [
    CommonModule,
    FormsModule,
    IonicModule,
    RouterModule.forChild(routes),
    CriacoesModule,
    FichasModule,
    PaginacaoModule,
    ToastModule,
    ImagemModule,
    UpdateNpcModule
  ],
  providers: [MessageService],
  declarations: [GerinimigosPage]
})
export class GerinimigosPageModule { }
