import { Component, OnInit, Input } from '@angular/core';
import { ModalController, AlertController } from '@ionic/angular';
import { FormBuilder, FormGroup, Validators } from '@angular/forms';
import { ItemService } from 'src/app/services/criacoes/item/item.service';
import { StorageService } from 'src/app/services/storage-service.service';
import { FichaModeloService } from 'src/app/services/fichas/ficha-modelo.service';
import { NpcService } from 'src/app/services/npc/npc.service';
import { ImagemComponent } from '../../imagem/imagem.component';
import { FichaService } from 'src/app/services/fichas/ficha.service';
import { NpcUpdateDTO } from 'src/app/models/Npc/npc-update.dto';
import { NpcBasicUpdateDTO } from 'src/app/models/Npc/npc-basic-update.dto';

@Component({
  selector: 'app-update-npc',
  templateUrl: './update-npc.component.html',
  styleUrls: ['./update-npc.component.scss'],
})
export class UpdateNpcComponent implements OnInit {

  formNpc: FormGroup;
  newNpc: NpcUpdateDTO;
  idFichaModelo = '';

  objetivos: string[] = [];
  objetivo: string = "";

  interesses: string[] = [];
  interesse: string = "";

  @Input('menu')
  menu: boolean = true;

  @Input('cancelarBTN')
  cancelarBTN: boolean = false;
  achado: boolean;

  loading: boolean = false;
  display: boolean = false;

  idCriado: string = '';

  requestUpdate: boolean = false;

  constructor(public modalCtrl: ModalController,
    public formBuilder: FormBuilder,
    private fmService: FichaModeloService,
    private npcService: NpcService,
    public alertController: AlertController,
    public fichaService: FichaService,
    public storage: StorageService,
    private modalController: ModalController) {
    this.formNpc = this.formBuilder.group({
      historia: ['', [Validators.required]],
      valorInventario: ['', [Validators.required, Validators.pattern('[0-9]*')]],
      tipoNpc: ['', [Validators.required]],
      isPrivate: ['', [Validators.required]],
    });
  }

  ngOnInit() {
    this.requestUpdate = false;
    this.loading = true;
    const id = this.npcService.getStaticID();
    this.npcService.findById(id)
      .subscribe(npc => {
        //console.log(npc);
        const { idNpc, historia, objetivos, interesses, isPrivate, tipoNpc, ficha } = npc;
        const { dinheiro } = ficha.inventario;


        this.formNpc.get('historia').setValue(historia);
        this.formNpc.get('tipoNpc').setValue(this.getCurrentType(tipoNpc));
        this.formNpc.get('valorInventario').setValue(dinheiro);
        if (isPrivate == false) {
          this.formNpc.get('isPrivate').setValue(false);
        } else {
          this.formNpc.get('isPrivate').setValue(true);
        }

        this.objetivos = objetivos;
        this.interesses = interesses;

        this.newNpc = {
          historia,
          interesses,
          isPrivate,
          objetivos,
          fichaUpdateDTO: ficha,
          tipoNpc,
          idNpc
        }
        this.loading = false;
      },
        error => {
          console.log(error)
          this.loading = false;
        });
  }

  ionViewDidLoad() {

  }

  salvar() {
    this.loading = true;
    this.newNpc.historia = this.formNpc.get('historia').value;
    this.newNpc.objetivos = this.objetivos;
    this.newNpc.interesses = this.interesses;
    this.newNpc.tipoNpc = this.formNpc.get('tipoNpc').value;
    this.newNpc.isPrivate = this.formNpc.get('isPrivate').value;

    const npcBasic: NpcBasicUpdateDTO = {
      historia: this.newNpc.historia,
      idNpc: this.newNpc.idNpc,
      interesses: this.newNpc.interesses,
      isPrivate: this.newNpc.isPrivate,
      objetivos: this.newNpc.objetivos,
      tipoNpc: this.newNpc.tipoNpc
    }

    this.npcService.updateBasic(npcBasic)
      .subscribe(res => {
        this.idCriado = res.ficha.idFicha;
        this.fichaService.setConfiguration(res.ficha, true, res.idNpc, null, res.tipoNpc, false);
        this.requestUpdate = true;
        this.showFicha();
        this.loading = false;
        this.achado = false;
      },
        error => {
          this.loading = false;
        });
  }

  getCurrentType(tipoNpc) {

    switch (tipoNpc) {
      case "ALIADO":
        return 0;
        break;

      case "INIMIGO":
        return 1;
        break;


      case "NEUTRO":
        return 2;
        break;

      case "BOSS":
        return 3;
        break;
    }
  }

  updateType(evt) {
    const tipo = evt.detail.value;
    this.formNpc.get('tipoNpc').setValue(tipo);
  }

  async showFicha() {
    this.display = true;
  }

  consultar() {
    this.loading = true;
    this.display = false;
    this.achado = false
    this.fmService.findFichaModeloById(this.idFichaModelo)
      .subscribe(res => {
        this.loading = false;
        this.achado = true
      },
        error => {
          this.loading = false;
          this.achado = false
        });
  }

  inserirObjetivo() {
    if (this.objetivo.length < 5) {
      this.presentError('objetivo');
    } else {
      this.objetivos.push(this.objetivo);
      this.objetivo = "";
    }
  }

  inserirInteresse() {
    if (this.interesse.length < 5) {
      this.presentError('interesse');
    } else {
      this.interesses.push(this.interesse);
      this.interesse = "";
    }

  }

  async presentError(texto: string) {
    const alert = await this.alertController.create({
      header: 'Valor inválido',
      message: 'Insira um ' + texto + '<br>Com pelo menos 5 caracteres',
      buttons: [
        {
          text: 'Ok',
          role: 'cancel',
          cssClass: 'secondary'
        }
      ]
    });

    await alert.present();
  }

  async img() {
    const modal = await this.modalController.create({
      component: ImagemComponent,
      backdropDismiss: false,
      cssClass: 'my-full',

    });

    return await modal.present();
  }

  close() {
    this.modalCtrl.dismiss(this.requestUpdate, null, 'princi');
  }

}
